import * as React from 'react';

export default (props: any) => {
    let totalScore = 0;
    let correctAnswers = 0;
    let percent = 0;
    // console.log(props.displayInputHistory);
    for (var i = 0; i < props.displayInputHistory.length; i++) {
        let hiragana = props.displayInputHistory[i].hiragana;
        let romaji = props.displayInputHistory[i].romaji;
        for (var j = 0; j < hiragana.length; j++) {
            totalScore++;
            if (hiragana[j] === romaji[j]) {
                correctAnswers++;
            }
        }
        percent = Math.floor(correctAnswers / totalScore * 10000) / 100;
    }
    return (
        <div className="scoreboard">
            <div>{correctAnswers} / {totalScore}</div>
            <div>{percent} %</div>
        </div>
    );
};


