import * as React from 'react';

// Interface
import { IDisplayInputHistoryProps } from 'src/interface';

// Components
import { HistoryListItem } from 'src/components/historyListItem';

export default (props: { historyList: IDisplayInputHistoryProps[] }) => {
    return (
        <div className="history-container">
            {
                props.historyList.map((item, index) => {
                    return (
                        <div key={index} className="history-list-item">
                            <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '-10px' }}>
                                <div>{`${item.date}`}</div>
                                <div>{`${(item.time / 1000).toFixed(2)}s`}</div>
                            </div>
                            <HistoryListItem romaji={item.hiragana} romajiInput={item.romaji} />
                        </div>
                    );
                })
            }
        </div>
    );
};


