import { Dispatch } from 'redux';

import { IDisplayInputHistoryProps } from 'src/interface';
// var constHiraganaObject = require('src/lib/hiragana.i18n.json');
// var constRomajiObject = require('src/lib/romaji.i18n.json');
// const constRomajiArray = Object.keys(constRomajiObject);

export const addHistory = (displayInput: IDisplayInputHistoryProps) => {
    // let displayInput = {
    //     hiragana: [],
    //     romaji: [],
    //     time: 1
    // };
    if (displayInput.romaji.length > 0) {
        return (dispatch: Dispatch<Object>) => {
            dispatch({
                type: 'ADD_HISTORY',
                displayInput
            });
        };
    }
    return (dispatch: Dispatch<Object>) => {
        // dispatch({
        //     type: 'ADD_HISTORY',
        //     displayInput
        // });
    };
};

export const loadHistory = (displayInput: IDisplayInputHistoryProps[]) => {
    return (dispatch: Dispatch<Object>) => {
        dispatch({
            type: 'LOAD_HISTORY',
            displayInput
        });
    };
};

export const storeTime = () => {
    return (dispatch: Dispatch<Object>) => {
        dispatch({
            type: 'STORE_TIME'
        });
    };
};


