import { Dispatch } from 'redux';

import { ISettingsProps } from 'src/interface';

export const changeLevel = (settings: ISettingsProps) => {
    return (dispatch: Dispatch<Object>) => {
        dispatch({
            type: 'CHANGE_SETTINGS',
            settings
        });
    };
};

export const changeMode = (mode: number) => {
    return (dispatch: Dispatch<Object>) => {
        dispatch({
            type: 'CHANGE_APP_MODE',
            mode
        });
    };
};


