import * as React from 'react';
import { Dispatch, bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { changeLevel, generateRomajiList, storeTime, clearRomajiInput } from 'src/actions';
import { ISettingsProps } from 'src/interface';

interface ISliderProps {
    settings: ISettingsProps;
    changeLevel: (level: ISettingsProps) => Dispatch<Object>;
    generateRomajiList: (level: number) => Dispatch<Object>;
    clearRomajiInput: () => Dispatch<Object>;
    storeTime: () => Dispatch<Object>;
}

class Slider extends React.Component<ISliderProps, {}> {
    handleChangeLevel = (e: any) => {
        this.props.changeLevel({ level: e.currentTarget.value });
        this.props.generateRomajiList(e.currentTarget.value);
        this.props.clearRomajiInput();
        this.props.storeTime();
    }
    render() {
        return (
            <div className="slider-container">
                <input type="range" min="1" max="10" value={this.props.settings.level} className="slider" onChange={this.handleChangeLevel} />
                <div className="slider-value">{this.props.settings.level}</div>
            </div>
        );
    }
}

function mapStateToProps(state: any) {
    return {
        settings: state.settings
    };
}
function mapDispatchToProps(dispatch: Dispatch<Object>) {
    return bindActionCreators(
        {
            changeLevel,
            generateRomajiList,
            storeTime,
            clearRomajiInput
        },
        dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(Slider);


