import * as React from 'react';
import { Dispatch, bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import store from 'src/reducers';

// Interface
import { ISettingsProps, IDisplayInputHistoryProps } from 'src/interface';

interface DashboardProps {
    settings: ISettingsProps;
    displayInputHistory: IDisplayInputHistoryProps;
}

// Components
import ScoreBoard from 'src/components/scoreBoard';
import Slider from './slider';
import SaveLoad from './saveload';

class Dashboard extends React.Component<DashboardProps, {}> {
    handleScoreClick = () => {
        console.log(store.getState());
    }
    render() {
        return (
            <div className="dashboard">
                {/* <div className="slidecontainer">
                    <input type="range" min="1" max="10"  className="slider"/>
                </div> */}
                <ScoreBoard displayInputHistory={this.props.displayInputHistory} />
                <Slider />
                {/* <button onClick={this.handleScoreClick}>Click</button> */}
                <SaveLoad />
            </div>
        );
    }
}

function mapStateToProps(state: any) {
    return {
        settings: state.settings,
        displayInputHistory: state.displayInputHistory
    };
}
function mapDispatchToProps(dispatch: Dispatch<Object>) {
    return bindActionCreators(
        {
            
        },
        dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);


