export const generatedRomajiList = (state: string[] = [], action: { type: string, generatedRomajiList: string[] }) => {
    switch (action.type) {
        case 'GENERATE_ROMAJI_LIST':
            return action.generatedRomajiList;
        default:
            return state;
    }
};

export const changeRomajiInput = (state: string = '', action: { type: string, input: string }) => {
    switch (action.type) {
        case 'CHANGE_ROMAJI_INPUT':
            return action.input;
        case 'CLEAR_ROMAJI_INPUT':
            return '';
        default:
            return state;
    }
};

export const toggleRomajiDisplay = (state = false, action: { type: string }) => {
    switch (action.type) {
        case 'TOGGLE_ROMAJI_DISPLAY':
            return !state;
        default:
            return state;
    }
};


