import * as React from 'react';
import * as ReactDOM from 'react-dom';
// Redux
import { Provider } from 'react-redux';

import store from './reducers';

import App from './App';
import './index.css';
import 'src/stylesheets/input.css';
import 'src/stylesheets/scoreboard.css';
import 'src/stylesheets/dashboard.css';
import 'src/stylesheets/app.css';
import 'src/stylesheets/history.css';
import 'src/stylesheets/statistics.css';
import 'src/stylesheets/modeButtons.css';
import registerServiceWorker from './registerServiceWorker';

ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>
  ,
  document.getElementById('root') as HTMLElement
);
registerServiceWorker();


