import { IDisplayInputHistoryProps } from 'src/interface';

export const displayInputHistory = (state: IDisplayInputHistoryProps[] = [], action: { type: string, displayInput: IDisplayInputHistoryProps | IDisplayInputHistoryProps[] }) => {
    switch (action.type) {
        case 'ADD_HISTORY':
            return state.concat(action.displayInput);
        case 'LOAD_HISTORY':
            return action.displayInput;
        default:
            return state;
    }
};

export const storeTime = (state: Date = new Date(), action: { type: string }) => {
    switch (action.type) {
        case 'STORE_TIME':
            return new Date();
        default:
            return state;
    }
};


