import * as React from 'react';

var constHiraganaObject = require('src/lib/hiragana.i18n.json');

export const HiraganaDisplay = (props: any) => {
    return (
        <div className="hiragana-screen">
            <ul className="hiragana-list">
                {
                    props.romaji.map((romaji: string, index: number) => {
                        return (
                            <li key={index} >
                                <div 
                                    className="hiragana-list-item" 
                                    onClick={props.toggleRomajiDisplay}
                                    style={{ color: props.romajiInput.indexOf(constHiraganaObject[romaji]) === index ? 'rgb(102,255,0)' : 'rgb(255,255,255)'}}
                                >
                                    {constHiraganaObject[romaji]}
                                </div>
                                {
                                    props.isRomajiDisplayed ?
                                        <div className="romaji-list-item">
                                            {romaji}
                                        </div> : null
                                }
                            </li>
                        );
                    })
                }
            </ul>
            {/* <ul className="romaji-list">
                {
                    props.romaji.map((romaji: string, index: number) => {
                        return <li key={index} className="romaji-list-item">{romaji}</li>;
                    })
                }
            </ul> */}
        </div>
    );
};


