// var constHiraganaObject = require('src/lib/hiragana.i18n.json');
var constRomajiObject = require('src/lib/romaji.i18n.json');

import { Dispatch } from 'redux';

export const generateRomajiList = (level: number = 5) => {
    if (level > 0 && level <= 10) {
        let constRomajiArray = Object.keys(constRomajiObject);
        let generatedRomajiList: string[] = [];

        while (generatedRomajiList.length < level) {
            let randomValue = Math.floor(Math.random() * constRomajiArray.length);
            let randomRomaji = constRomajiArray[randomValue];
            if (generatedRomajiList.indexOf(randomRomaji) === -1) {
                generatedRomajiList.push(randomRomaji);
            }
        }

        return (dispatch: Dispatch<Object>) => {
            dispatch({
                type: 'GENERATE_ROMAJI_LIST',
                generatedRomajiList
            });
        };

    }
    return (dispatch: Dispatch<Object>) => {
        // dispatch({
        //     type: 'GENERATE_ROMAJI_LIST',
        //     categoryItem
        // });
    };
};

export const toggleRomajiDisplay = () => {
    return (dispatch: Dispatch<Object>) => {
        dispatch({
            type: 'TOGGLE_ROMAJI_DISPLAY',
        });
    };
};


