import * as React from 'react';
import { Dispatch, bindActionCreators } from 'redux';
import { connect } from 'react-redux';

// Interface
import { IDisplayInputHistoryProps } from 'src/interface';

// Components
import HiraganaGenerator from 'src/containers/hiraganaGenerator';
import RomajiInput from 'src/containers/romajiInput';
import HistoryList from 'src/components/historyList';
import Statistics from 'src/containers/statistics';
import ModeButtons from 'src/components/modeButtons';

// Actions
import { changeMode } from 'src/actions';

interface IHistoryListProps {
    appMode: number;
    historyList: IDisplayInputHistoryProps[];
    changeMode: (mode: number) => Dispatch<object>;
}

class AppContainer extends React.Component<IHistoryListProps, {}> {
    handleButtonClick = (mode: number) => {
        this.props.changeMode(mode);
    }
    render() {
        return (
            <div className="app-container">
                <ModeButtons handleButtonClick={this.handleButtonClick} appMode={this.props.appMode} />
                {
                    this.props.appMode === 1 ?
                        <div>
                            <HiraganaGenerator />
                            <RomajiInput />
                        </div> :
                        this.props.appMode === 2 ?
                            <HistoryList historyList={this.props.historyList} /> :
                            <Statistics />
                }
            </div>
        );
    }
}

function mapStateToProps(state: any) {
    return {
        historyList: state.displayInputHistory,
        appMode: state.appMode
    };
}
function mapDispatchToProps(dispatch: Dispatch<Object>) {
    return bindActionCreators(
        {
            changeMode
        },
        dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(AppContainer);


