import { ISettingsProps } from 'src/interface';

const defaultSettings = {
    level: 5
};

export const settings = (state: ISettingsProps = defaultSettings, action: { type: string, settings: ISettingsProps }) => {
    switch (action.type) {
        case 'CHANGE_SETTINGS':
            return {
                ...state,
                ...action.settings
            };
        default:
            return state;
    }
};

export const appMode = (state: number = 1, action: { type: string, mode: number }) => {
    switch (action.type) {
        case 'CHANGE_APP_MODE':
            return action.mode;
        default:
            return state;
    }
};


