import * as React from 'react';
import { Dispatch, bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import store from 'src/reducers';

import { loadHistory, changeLevel, storeTime, generateRomajiList, clearRomajiInput } from 'src/actions';
import { IDisplayInputHistoryProps, ISettingsProps } from 'src/interface';

interface ISaveLoadProps {
    loadHistory: (displayInput: IDisplayInputHistoryProps[]) => Dispatch<Object>;
    generateRomajiList: (level: number) => Dispatch<Object>;
    clearRomajiInput: () => Dispatch<Object>;
    changeLevel: (settings: ISettingsProps) => Dispatch<Object>;
    storeTime: () => Dispatch<Object>;
}

class SaveLoad extends React.Component<ISaveLoadProps, {}> {
    onSaveClick = () => {
        let data: any = store.getState();
        let saveData = {
            displayInputHistory: data.displayInputHistory,
            settings: data.settings
        };
        localStorage.setItem('hiragana', JSON.stringify(saveData));
    }
    onLoadClick = () => {
        let data = localStorage.getItem('hiragana');
        let loadData = data ? JSON.parse(data !== null ? data : '{}') : null;
        if (loadData) {
            this.props.loadHistory(loadData.displayInputHistory);
            this.props.changeLevel({ level: loadData.settings.level });
            this.props.generateRomajiList(loadData.settings.level);
            this.props.clearRomajiInput();
            this.props.storeTime();
        }
    }
    // onDeleteClick = () => {
    //     localStorage.removeItem('hiragana');
    // }
    render() {
        return (
            <div>
                <button className="save-button" onClick={this.onSaveClick}>Save</button>
                <button className="load-button" onClick={this.onLoadClick}>Load</button>
                {/* <button className="load-button" onClick={this.onDeleteClick}>Delete</button> */}
            </div>
        );
    }
}

function mapStateToProps(state: any) {
    return {

    };
}
function mapDispatchToProps(dispatch: Dispatch<Object>) {
    return bindActionCreators(
        {
            loadHistory,
            changeLevel,
            storeTime,
            generateRomajiList,
            clearRomajiInput
        },
        dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(SaveLoad);


