import * as React from 'react';
import { Dispatch, bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { generateRomajiList, changeRomajiInput, clearRomajiInput, addHistory, storeTime } from 'src/actions';
var constHiraganaObject = require('src/lib/hiragana.i18n.json');

// Interface
import { IDisplayInputHistoryProps } from 'src/interface';
// Interface
import { ISettingsProps } from 'src/interface';

interface RomajiInputProps {
    settings: ISettingsProps;
    prevTime: Date;
    generatedRomajiList: string[]; // Generated Romaji List
    inputValue: string;
    generateRomajiList: (level: number) => Dispatch<Object>;
    changeRomajiInput: (input: string, generatedRomajiList: string[]) => Dispatch<Object>;
    clearRomajiInput: () => Dispatch<Object>;
    addHistory: (displayInput: IDisplayInputHistoryProps) => Dispatch<Object>;
    storeTime: () => Dispatch<Object>;
}

class RomajiInput extends React.Component<RomajiInputProps, {}> {
    reverseObjectKeyValue = (object: any) => {
        let formattedObject = {};
        for (var key in object) {
            if (key) {
                formattedObject[object[key]] = key;
            } else {
                formattedObject[object[key]] = key;
            }
        }
        return formattedObject;
    }
    todayDate = () => {
        let today = new Date();
        let year = today.getFullYear();
        let month = today.getMonth() < 9 ? `0${today.getMonth() + 1}` : today.getMonth() + 1;
        let day = today.getDate() ? `0${today.getDate()}` : today.getDate();
        return `${day}/${month}/${year}`;
    }
    handleSubmit = (event: any) => {
        event.preventDefault();
        // handle form submit here
        // let generatedHiraganaList = this.props.generatedRomajiList.map((romaji) => {
        //     return constHiraganaObject[romaji];
        // }).join('');
        let hiraganaToRomaji = this.reverseObjectKeyValue(constHiraganaObject);
        let inputRomajiArray = this.props.inputValue.split('').map((hiragana) => {
            return hiraganaToRomaji[hiragana];
        });
        // console.log(this.props.generatedRomajiList);
        // if (this.props.inputValue === generatedHiraganaList) {
        this.props.generateRomajiList(this.props.settings.level);
        this.props.clearRomajiInput();
        this.props.storeTime();
        // }
        // 1. Check correct
        // 2. Check number of wrong answers
        // 3. Store score
        // 4. Store wrong answers
        let time = new Date().getTime() - this.props.prevTime.getTime();
        let displayInput = {
            // hiragana: generatedHiraganaList.split(''),
            // romaji: this.props.inputValue.split(''),
            hiragana: this.props.generatedRomajiList,
            romaji: inputRomajiArray,
            time,
            date: this.todayDate()
        };
        this.props.addHistory(displayInput);
    }
    render() {
        return (
            <form className="romaji-input" onSubmit={this.handleSubmit}>
                <input type="text" onChange={(e) => this.props.changeRomajiInput(e.currentTarget.value, this.props.generatedRomajiList)} value={this.props.inputValue} />
            </form>
        );
    }
}

function mapStateToProps(state: any) {
    return {
        settings: state.settings,

        generatedRomajiList: state.generatedRomajiList,
        inputValue: state.changeRomajiInput,
        prevTime: state.storeTime
    };
}
function mapDispatchToProps(dispatch: Dispatch<Object>) {
    return bindActionCreators(
        {
            generateRomajiList,
            changeRomajiInput,
            clearRomajiInput,
            addHistory,
            storeTime
        },
        dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(RomajiInput);


