import * as React from 'react';

export default (props: any) => {
    return (
        <div className="mode-buttons-container">
            <button onClick={() => props.handleButtonClick(1)} className={props.appMode === 1 ? 'active-mode-button' : ''}>App</button>
            <button onClick={() => props.handleButtonClick(2)} className={props.appMode === 2 ? 'active-mode-button' : ''}>History</button>
            <button onClick={() => props.handleButtonClick(3)} className={props.appMode === 3 ? 'active-mode-button' : ''}>Statistics</button>
        </div>
    );
};


