import * as React from 'react';
import { Dispatch, bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { HiraganaDisplay } from 'src/components/hiraganaDisplay';

// Actions
import { generateRomajiList, toggleRomajiDisplay, storeTime } from 'src/actions';
// Interface
import { ISettingsProps } from 'src/interface';

interface HiraganaGeneratorProps {
    settings: ISettingsProps;
    romajiList: string[];
    isRomajiDisplayed: boolean;
    romajiInput: string;
    generateRomajiList: (level: number) => Dispatch<Object>;
    toggleRomajiDisplay: () => Dispatch<Object>;
    storeTime: () => Dispatch<Object>;
}

class HiraganaGenerator extends React.Component<HiraganaGeneratorProps, {}> {
    componentDidMount() {
        this.props.generateRomajiList(this.props.settings.level);
        this.props.storeTime();
    }
    render() {
        return (
            <div className="hiragana-generator">
                <HiraganaDisplay romaji={this.props.romajiList} toggleRomajiDisplay={this.props.toggleRomajiDisplay} isRomajiDisplayed={this.props.isRomajiDisplayed} romajiInput={this.props.romajiInput} />
            </div>
        );
    }
}

function mapStateToProps(state: any) {
    return {
        settings: state.settings,
        romajiList: state.generatedRomajiList,
        isRomajiDisplayed: state.toggleRomajiDisplay,
        romajiInput: state.changeRomajiInput
    };
}
function mapDispatchToProps(dispatch: Dispatch<Object>) {
    return bindActionCreators(
        {
            generateRomajiList,
            toggleRomajiDisplay,

            storeTime
        },
        dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(HiraganaGenerator);


