import * as React from 'react';
import { Dispatch, bindActionCreators } from 'redux';
import { connect } from 'react-redux';

// Interface
import { IDisplayInputHistoryProps } from 'src/interface';

interface IStatisticsProps {
    historyList: IDisplayInputHistoryProps[];
}

interface IStatisticsState {
    statisticsArray: IMonthlyObjectProps[];
}

interface IMonthlyObjectProps {
    month: string;
    wrongAnswersObject: object;
    totalAnswers: number;
    totalCorrectAnswers: number;
    totalTime: number;
    averageAccuracy: string;
    averageTime: number;
}

class Statistics extends React.Component<IStatisticsProps, IStatisticsState> {
    constructor(props: any) {
        super(props);
        this.state = {
            statisticsArray: []
        };
    }
    componentDidMount() {
        this.setState({
            statisticsArray: this.getStatisticsArray()
        });
    }
    componentDidUpdate(prevProps: any, prevState: any, snapshot: any) {
        let statisticsArray = this.getStatisticsArray();
        if (!this.checkEquivalent(prevState.statisticsArray, statisticsArray)) {
            this.setState({
                statisticsArray: this.getStatisticsArray()
            });
        }
    }
    checkEquivalent = (arrayA: IMonthlyObjectProps[], arrayB: IMonthlyObjectProps[]) => {
        if (arrayA.length !== arrayB.length) {
            return false;
        }
        for (var i = 0; i < arrayA.length; i++) {
            if (
                arrayA[i].averageAccuracy !== arrayB[i].averageAccuracy         || arrayA[i].averageTime !== arrayB[i].averageTime      ||
                arrayA[i].month !== arrayB[i].month                             || arrayA[i].totalAnswers !== arrayB[i].totalAnswers    ||
                arrayA[i].totalCorrectAnswers !== arrayB[i].totalCorrectAnswers || arrayA[i].totalTime !== arrayB[i].totalTime
            ) {
                return false;
            }
        }
        return true;
    }
    sortDates = (firstDate: any, secondDate: any) => {
        let firstDateArray = firstDate.month.split('/');
        let secondDateArray = secondDate.month.split('/');
        let firstDateValue = Number(firstDateArray[0]) + 12 * Number(firstDateArray[1]);
        let secondDateValue = Number(secondDateArray[0]) + 12 * Number(secondDateArray[1]);
        return secondDateValue - firstDateValue;
    }
    getDateObject = (date: string) => {
        let dateArray = date.split('/').slice(1);
        let dateObject = {};
        for (var i = 0; i < 12; i++) {
            dateObject[dateArray.join('/')] = [];
            if (Number(dateArray[0]) <= 1) {
                dateArray[0] = `12`;
                dateArray[1] = `${Number(dateArray[1]) - 1}`;
            } else {
                dateArray[0] = `${Number(dateArray[0]) <= 10 ? '0' : ''}${Number(dateArray[0]) - 1}`;
            }
        }
        return dateObject;
    }
    getStatistics = (dateObject: any) => {
        let statisticsObject: object = {};
        for (var month in dateObject) {
            if (dateObject[month].length > 0) {
                let monthlyObject = {
                    month,
                    wrongAnswersObject: {},
                    totalAnswers: 0,
                    totalCorrectAnswers: 0,
                    totalTime: 0,
                    averageAccuracy: `0%`,
                    averageTime: 0
                };
                dateObject[month].forEach((historyItem: IDisplayInputHistoryProps) => {
                    for (var i = 0; i < historyItem.hiragana.length; i++) {
                        if (historyItem.hiragana[i] === historyItem.romaji[i]) {
                            monthlyObject.totalCorrectAnswers++;
                        } else {
                            monthlyObject.wrongAnswersObject[historyItem.hiragana[i]] = [];
                            monthlyObject.wrongAnswersObject[historyItem.hiragana[i]].push(historyItem.romaji[i]);
                        }
                    }
                    monthlyObject.totalTime += historyItem.time;
                    monthlyObject.totalAnswers += historyItem.hiragana.length;
                });
                monthlyObject.totalTime = Math.floor(monthlyObject.totalTime / 10) / 100;
                monthlyObject.averageAccuracy = `${Math.floor(monthlyObject.totalCorrectAnswers * 10000 / monthlyObject.totalAnswers) / 100}%`;
                monthlyObject.averageTime = Math.floor(monthlyObject.totalTime * 100 / monthlyObject.totalCorrectAnswers) / 100;
                statisticsObject[month] = monthlyObject;
            }
        }
        let statisticsArray: IMonthlyObjectProps[] = [];
        for (var key in statisticsObject) {
            if (key) {
                statisticsArray = statisticsArray.concat(statisticsObject[key]);
            }
        }
        return statisticsArray.sort(this.sortDates);
    }
    getStatisticsArray = () => {
        let newestEntry = this.props.historyList[this.props.historyList.length - 1];
        let statisticsArray = [];
        if (newestEntry) {
            let dateObject = this.getDateObject(newestEntry.date);
            for (var i = 0; i < this.props.historyList.length; i++) {
                let date = this.props.historyList[i].date.split('/').slice(1).join('/');
                dateObject[date] = dateObject[date].concat(this.props.historyList[i]);
            }
            statisticsArray = this.getStatistics(dateObject);
            return statisticsArray;
        }
        return [];
    }
    render() {
        return (
            <div className="statistics-container">
                <table className="statistics-table">
                    <tr>
                        <th>Date</th>
                        <th>Accuracy</th>
                        <th>Total Answers</th>
                        <th>Average Time</th>
                    </tr>
                    {
                        this.state.statisticsArray.map((statistics, index) => {
                            return (
                                <tr key={index}>
                                    <td>{statistics.month}</td>
                                    <td>{statistics.averageAccuracy}</td>
                                    <td>{statistics.totalAnswers}</td>
                                    <td>{statistics.averageTime}</td>
                                </tr>
                            );
                        })
                    }
                </table>
            </div>
        );
    }
}

function mapStateToProps(state: any) {
    return {
        historyList: state.displayInputHistory
    };
}
function mapDispatchToProps(dispatch: Dispatch<Object>) {
    return bindActionCreators(
        {

        },
        dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(Statistics);


