import * as React from 'react';

var constHiraganaObject = require('src/lib/hiragana.i18n.json');

export const HistoryListItem = (props: any) => {
    return (
        <ul className="history-list">
            {
                props.romaji.map((romaji: string, index: number) => {
                    return (
                        <li key={index} >
                            <div 
                                className="hiragana-list-item" 
                                // style={{ color: props.romajiInput.indexOf(romaji) >= 0 ? 'rgb(102,255,0)' : 'rgb(202,0,42)'}}
                            >
                                {constHiraganaObject[romaji]}
                            </div>
                            <div className="history-romaji-generated-item">
                                {romaji}
                            </div>
                            <div 
                                className="history-romaji-input-item"
                                style={{ color: props.romajiInput.indexOf(romaji) === index ? 'rgb(102,255,0)' : 'rgb(202,0,42)'}}
                            >
                                {props.romajiInput[index]}
                            </div>
                        </li>
                    );
                })
            }
        </ul>
    );
};


